body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  margin: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ol, ul {
  padding-left: 30px;
}
.square {
  background: #fff;
  border: 1px solid #999;
  line-height: 34px;
  height: 34px;
  width: 34px;
  float: left;
  font-size: 24px;
  font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}
.square:focus {
    outline: none;
}
.status {
    margin-bottom: 10px;
}
.board-row:after {
    clear: both;
    content: "";
    display: table;
}
.game {
    display: flex;
    flex-direction: row;
}
.game-info {
    margin-left: 20px;
}
.game-info li button:focus {
    font-weight: 700;
}
.game-info li button:active {
    font-weight: 700;
}
